@font-face { // Поключаем шрифт Oswald regular
  font-family: "Oswald";
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face { // Поключаем шрифт Oswald medium
  font-family: "Oswald";
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

body {
  box-sizing: border-box;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;

  font-family: "Oswald", "Arial", sans-serif;

  color: #000000;
  background-color: #ffffff;
}

.button { // Общаяя стилизация кнопок
  display: block;

  font-size: 14px;
  line-height: 14px;

  text-decoration: none;
  text-align: center;
  text-transform: uppercase;

  border: none;

  background-color: #68b738;
  color: #ffffff;
}

.button:hover,
.button:focus { // Реакция при наведении(фокусе) на кнопку
  background-color: #5eaa2f;
}

.button:active { // Реакция при нажатии на кнопку
  color: rgba(255, 255, 255, 0.3);
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden { // Доступно скрываем элементы
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img { // Для всех изображений
  max-width: 100%;
  height: auto;
}
