.before-after { // Внутренний блок с картинокй кота (до-после) и "ползунком"
  box-sizing: border-box;

  width: 100%;
}

.before-after__cat--fat { // Картинко толстого кота
  display: block;

  width: 320px;
  height: 240px;

  margin: 0 auto;
}

.before-after__cat--slim { // Картинка худого кота
  display: none;
}

.before-after__range-control { // Блок с "ползунком"
  position: relative;

  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;

  margin: 32px 0 36px 5px;
}

.before-after__scale { // Шкала ползунка (внешняя серая часть)
  flex-grow: 0;
  flex-shrink: 0;

  width: 83px;
  height: 12px;

  margin: 0 17px;

  border-radius: 6px;
  border: 1px solid #cdcdcd;

  background-color: #ffffff;
}

.before-after__bar { // Шкала ползунка (зелёный индикатор)
  width: 42%;
  height: 6px;

  margin: 3px 0 1px 6px;

  border-radius: 10px;

  background-color: #68b738;
}

.before-after__text { // Надписи около шкалы (До ------------ После)
  display: block;
  position: relative;

  margin: 0;
  padding: 0;

  border: none;

  background: none;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.before-after__range-toggle { // Кнопка на ползунке
  display: none;

  position: absolute;

  top: 54px;
  left: 125px;

  width: 32px;
  height: 32px;

  border: none;
  border-radius: 50%;

  font-size: 0;

  background-color: #ffffff;

  box-shadow: 0 1px 3px 2px #cdcdcd;
}

.before-after__range-toggle::after { // Зелёная точка на кнопке ползунка
  content: "";

  position: absolute;

  top: 12px;
  left: 12px;

  width: 8px;
  height: 8px;

  border-radius: 50%;

  background-color: #68b738;
}

.before-after__range-toggle:hover,
.before-after__range-toggle:focus { // Реакция кнопки полжунка при наведении(фокусе)
  box-shadow: 0 2px 4px 3px #cdcdcd;
}

.before-after__range-toggle:active { // Реакция кнопки полжунка при нажатии
  border: 2px solid #68b738;
}

.before-after__range-toggle:active::after { // Добавили границу - переопределили положение "зелёной точки"
  top: 10px;
  left: 10px;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .before-after { // Меняем размер и отступы у блока
    position: relative;

    width: 100%;

    margin-top: 221px;

    padding: 70px 0 66px 0;

    background-color: #f2f2f2;

    z-index: -3;
  }

  .before-after::after { // "Тянущийся блок с фоном для худого кота"
    content: "";

    position: absolute;

    top: 0;
    right: 0;

    width: calc(50% - 10px);
    height: 462px;

    background-color: #eaeaea;

    z-index: -2;
  }

  .before-after__container { // Конетйнер для котов и ползунка
    position: relative;

    margin: 0 auto;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;

    width: 708px;
    height: 327px;
    min-height: 327px;
  }

  .before-after__image { // Общие стили для блоков, внутри которых картинки с котами
    position: absolute;

    border: none;
  }

  .before-after__image--before { // Контейнер для картинки с толстым котом
    top: -239px;
    left: 26px;

    width: 338px;

    min-height: 632px;

    overflow: hidden;

    z-index: 1;

    background-color: #f2f2f2;
    background-image: linear-gradient(180deg, rgba(255, 255, 255, 1) 169px, rgba(242, 242, 242, 1) 169px);
  }

  .before-after__image--after { // Контейнер для картинки с худым котом
    top: -239px;
    left: 26px;

    min-height: 632px;

    background-color: #eaeaea;
    background-image: linear-gradient(180deg, rgba(255, 255, 255, 1) 169px, rgba(234, 234, 234, 1) 169px);
  }

  .before-after__cat--fat { // Картинка толстого кота
    width: 690px;
    max-width: 690px;
    height: 517px;

    margin: 0;
  }

  .before-after__cat--slim { // Картинка худого кота
    display: block;

    width: 690px;
    max-width: 690px;
    height: 517px;

    margin: 0;
  }

  .before-after__range-control { // Ползунок
    width: 570px;

    margin: 14px auto 4px auto;

    padding-left: 20px;

    z-index: 1;
  }

  .before-after__scale { // Шкала ползунка (внешняя серая часть)
    width: 430px;

    margin: 0 20px 0 20px;

    background-color: transparent;

    border: none;
  }

  .before-after__bar { // Индикатор ползунка
    width: 100%;

    margin: 3px 0 0 0;

    background-color: #dcdcdc;
  }

  .before-after__range-toggle { // Кнопка ползунка
    display: block;

    top: 0;
    left: 289px;
  }

  .before-after__text { // Надписи около шкалы (До ------------ После)
    padding: 5px 0;

    font-size: 20px;

    border-bottom: 1px dotted #bebebe;
  }

  .before-after__text:hover,
  .before-after__text:focus { // Реакция кнопок "До" "После" при наведении(фокусе)
    border-bottom: 1px solid #bebebe;
  }

  .before-after__text:active { // Реакция кнопок "До" "После" при нажатии
    border-color: transparent;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .before-after { // Меняем размер и отступы у блока
    position: inherit;

    width: 693px;

    margin: 0;

    z-index: 0;
  }

  .before-after::after { // "Тянушийся" фон
    width: calc(50% - 335px);

    z-index: -1;
  }

  .before-after__container { // Контейнер для котов и ползунка
    justify-content: flex-end;

    width: 690px;
  }

  .before-after__image--before { // Контейнер для картинки с толстым котом
    top: -259px;
    left: 40px;

    width: 365px;
    min-height: 652px;

    background-image: linear-gradient(180deg, rgba(255, 255, 255, 1) 189px, rgba(242, 242, 242, 1) 189px);
  }

  .before-after__image--after { // Контейнер для картинки с худым
    width: 652px;
    min-height: 652px;

    top: -259px;
    left: 40px;

    background-image: linear-gradient(180deg, rgba(255, 255, 255, 1) 189px, rgba(234, 234, 234, 1) 189px);

    overflow: hidden;
  }

  .before-after__range-control { // Шкала до после
    margin: 0 0 3px 0;
    padding: 0;
  }

  .before-after__range-toggle { // Кнопка ползунка
    left: 269px;
  }
}
