.page-nav { // Блок главного меню
  width: 100%;
  margin-top: 15px;
}

.page-nav__list { // Список главного меню
  margin: 0;
  padding: 0;

  list-style: none;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.page-nav__item { // Центруем надписи меню
  text-align: center;
  border-top: 1px solid #e6e6e6;
}

.page-nav__option { // Ссылка каждого элемента меню
  display: block;
  width: 100%;

  padding: 23px 0 22px 0;

  font-size: 20px;
  line-height: 20px;
  font-weight: 500;

  text-transform: uppercase;
  text-decoration: none;

  color: #000000;
}

@media (max-width: 767px) {
  .page-nav__list--js {
    display: none;
  }
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .page-nav {  // Пока под вопросом
    margin-top: 18px;
    width: 440px;
  }

  .page-nav__list { // Меняем направление главной оси флекса
    flex-direction: row;
  }

  .page-nav__item {
    border: none;
  }

  .page-nav__option { // Отступы ссылкам (паддинги будут совпадать с паддингами логотипа)
    padding: 0;
    padding-bottom: 8px;

    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }

  .page-nav__option--current {
    border-bottom: 2px solid #68b738;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .page-nav {
    margin-top: 19px;
    width: 480px;
  }

  .page-nav__option { // Отступы ссылкам (паддинги будут совпадать с паддингами логотипа)
    font-size: 20px;
    line-height: 24px;

    color: #ffffff;
  }

  .page-nav__option:hover,
  .page-nav__option:focus { // Реакция при наведении и фокусе на пункте меню
    opacity: 0.6;
  }

  .page-nav__option:active { // Реакция при нажатии на пункте меню
    opacity: 0.3;
  }

  .page-nav__option--white {
    border-color: #ffffff;
  }

  .page-nav__option--clear {
    color: #000000;
  }
}
