.food-type { // Блок "Тип программы" (Похудение, набор массы)
  margin: 22px auto 0 auto;
  width: 320px;
}

.food-type__list { // Сброс стиля у списка
  margin: 0 20px;
  padding: 0;

  list-style: none;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.food-type__item { // Элемент списка (<li>)
  box-sizing: border-box;

  position: relative;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;

  margin-bottom: 20px;
  padding: 23px 20px 0 20px;

  background-color: #f2f2f2;
}

.food-type__cat { // Контейнер для картинки с котом
  position: absolute;

  width: 50px;
  height: 50px;

  top: 25px;
  left: 21px;

  border-radius: 50%;

  background-color: #68b738;
}

.food-type__image { // Картинка с котом
  position: absolute;

  top: -26px;
  left: -9px;

  transform: scale(0.5);
}

.food-type__item:nth-child(2) .food-type__image { // Картинка с котом (icon-cat-pro)
  top: -3px;
  left: -42px;
}

.food-type__header { // Заголовок блока food-type
  margin: 7px 0 4px 71px;
  padding: 0;


  font-size: 24px;

  line-height: 37px;
  font-weight: 300;

  text-transform: uppercase;
}

.food-type__info { // информация о программе
  width: 240px;

  margin-top: 25px;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.food-type__catalog { // Ссылка на каталог
  position: relative;
  width: 100%;

  align-self: flex-start;

  margin-top: 7px;
  margin-bottom: 22px;
  padding-top: 20px;

  text-decoration: none;
  text-transform: uppercase;

  font-size: 16px;
  line-height: 16px;

  color: #000000;

  border-top: 1px solid #d9d9d9;
}

.food-type__catalog::after { // Стрелочка около ссылки
  content: "";

  position: absolute;

  bottom: 3px;
  left: 96px;

  width: 50px;
  height: 11px;

  background-image: url("../img/icon-right-arrow-long.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}

.food-type__catalog:hover::after,
.food-type__catalog:focus::after { // Реакия при наведении(фокусе) на ссылку "Программа..."

  background-image: url("../img/icon-right-arrow-verylong.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 32px 11px;
}

.food-type__catalog:active { // Реакция при нажатии на ссылку "Программа..."
  opacity: 0.3;
}


@media (min-width: 768px) { // Если экран больше 768px ----->
  .food-type {
    width: 708px;
    padding-left: 20px;
  }

  .food-type__list {
    margin: 0;
  }

  .food-type__item { // Делаем фиксированный размер карточки

    width: 708px;
    min-height: 370px;

    padding-left: 53px;
    margin-bottom: 32px;
  }

  .food-type__cat { // Позиционируем блок с картинкой
    top: 92px;
    left: 446px;

    width: 200px;
    height: 200px;
  }

  .food-type__image { // Позиция картинки с котом относительно его контейнера
    top: 36px;
    left: 65px;

    transform: scale(2);
  }

  .food-type__item:nth-child(2) .food-type__image { // Позиция icon-cat-pro
    top: 72px;
    left: 32px;

    transform: scale(2);
  }

  .food-type__header { // Заголовок блока
    width: 300px;
    margin: 53px 0 0 0;

    font-size: 36px;
    line-height: 37px;
  }

  .food-type__info { // Описание блока
    width: 375px;

    font-size: 16px;
    line-height: 24px;
  }

  .food-type__item:nth-child(2) .food-type__info { // Подгоняем под макет размер блока описания у второй карточки
    width: 340px;
  }

  .food-type__catalog { // Ссылка на каталог
    width: 160px;
    margin-top: 41px;
    padding: 0;

    font-size: 20px;
    line-height: 20px;
    font-weight: 400;

    border: none;
  }

  .food-type__catalog::after {
    bottom: 5px;
    left: 128px;
  }

  .food-type__catalog:nth-child(3)::after {
    bottom: 50px;
    left: 131px;
  }
}

@media (min-width: 1300px) {  // Если экран больше 1300px ----->
  .food-type { // размер всей секции
    width: 100%;

    padding: 0;
  }

  .food-type__list { // Размер внутреннего блока
    width: 1220px;

    margin: 0 auto;
    padding: 0 0 0 20px;
  }

  .food-type__item { // Фиксируем размер карточки
    box-sizing: border-box;

    width: 570px;
  }

  .food-type__cat { // Позиционируем блок с картинкой + размеры
    top: 47px;
    left: 52px;

    width: 100px;
    height: 100px;
  }

  .food-type__image { // Позиционируем и уменьшаем картинку
    top: -5px;
    left: 15px;

    transform: scale(1);
  }

  .food-type__item:nth-child(2) .food-type__image { // Позиционируем icon-cat-pro
    top: 22px;
    left: -17px;

    transform: scale(1);
  }

  .food-type__header { // Размер заголовка
    width: auto;

    margin: 52px 0 50px 160px;
  }

  .food-type__info { // Блок описания (Фиксируем размер)
    width: 465px;
  }

  .food-type__item:nth-child(2) .food-type__info { // Блок описания (Фиксируем размер)
    width: 465px;
  }

  .food-type__catalog { // Доп отступ
    margin-top: 15px;
  }
}
