.copyright { // Блок "Copyright"
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  width: 280px;

  padding-bottom: 20px;
}

.copyright__link { // Ссылки
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  width: 280px;

  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;

  text-decoration: none;
  color: #444444;
}

.copyright__image { // Размер логотипа HTML Academy
  width: 40px;
  height: 42px;

  margin-right: -6px;

  fill: #666666;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .copyright { // Размер блока copyright
    width: 162px;

    margin: 0;
  }


  .copyright:hover .copyright__image,
  .copyright:focus .copyright__image { // Изменение цвета при наведении
    fill: #68b738;
  }

  .copyright:active .copyright__link { // Изменение прозрачности при нажатии
    opacity: 0.5;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .copyright__image {
    margin-right: -1px;
  }
}
