.goods { // Карточка товара
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-start;

  width: 280px;

  padding: 0 20px;
  margin: 0 auto;
}

.goods__header { // Заголовок карточки
  margin: 21px 0 13px 0;
  padding: 0;

  width: 75px;

  font-size: 16px;
  line-height: 20px;
  font-weight: 300;

  text-transform: uppercase;
}

.goods__list { // Список характеристик товара (Вкус, вес, стоимость)
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;

  width: 140px;

  margin: 0;
  padding: 0;

  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
}

.goods__item { // Список значений товара
  margin: 0 0 0 auto;
  padding: 0;
}

.goods__wrapper { // Обёртка для описания товара
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.goods__image {
  width: 140px;
  height: 144px;
}

.goods__container { // Контейнер для списка характеристик
  display: flex;
  justify-content: space-between;

  width: 140px;
}

.goods__button { // Кнопка "Заказать"
  display: block;

  width: 100%;

  margin: 0 0 24px 0;
  padding: 10px;

  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .goods { // Карточка товара
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    width: 324px;

    margin: 138px 0 0 0;
    padding: 0;

    min-height: 430px;

    background-color: #f2f2f2;
  }

  .goods--show-more {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    background-color: #ffffff;

    border: 1px solid #e6e6e6;

    height: 430px;
  }

  .goods__header { // Заголовок
    width: 130px;

    margin: 18px auto 20px auto;

    font-size: 20px;
    line-height: 20px;

    text-align: center;
  }

  .goods__container { // Обёртка для списков "Объем____500 г"
    width: 250px;

    padding: 7px 0 8px 0;

    border-bottom: 1px solid #cdcdcd;
  }

  .goods__list {  // Список характеристик товара
    width: 250px;

    margin-bottom: 33px;

    font-size: 16px;
    line-height: 16px;
  }

  .goods__image {
    width: 324px;
    height: 208px;

    margin-top: -46px;
    margin-bottom: -16px;
  }

  .goods__image--big {
    margin-top: -81px;
    margin-bottom: 18px;
  }

  .goods__button { // Кнопка "Заказать"
    width: 230px;

    margin: 0 0 40px 0;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .goods { // Меняем размер карточки товара
    width: 245px;
    margin-bottom: 4px;
  }

  .goods__list {
    width: 245px;
  }

  .goods__header {
    margin-bottom: 19px;
  }

  .goods__container {// Меняем размер
    width: 170px;
    margin: 0 auto;
  }

  .goods__image {
    width: 245px;
    // height: 208px;
  }

  .goods__button { // Меняем размер
    width: 150px;
  }
}
