.promo { // Блок с картинкой и значком подарка
  margin: 0;
  padding: 0;

  width: 100%;
  height: 289px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;

  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-position: center;
  background-repeat: no-repeat;

  background-color: #68b738;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
    background-image: url("../img/gift-photo-mobile@2x.jpg");
    background-size: 280px auto;

    background-color: #68b738;
  }
}

.promo__image { // Значек "Подарок"
  display: block;
  width: 55px;
  height: 60px;

  padding-top: 34px;
}

.promo__info { // Описание промо акции
  margin: 0;
  padding: 0 0 10px 0;

  width: 170px;

  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;

  text-align: center;


  color: #ffffff;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .promo { // Меняем блок промо
    flex-direction: row;

    height: 200px;
    background-image: url("../img/gift-photo-tablet@1x.jpg");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
      background-image: url("../img/gift-photo-tablet@1x.jpg");
      background-size: 708px auto;
    }
  }

  .promo__image { // значёк Подарок
    padding: 0 100px 10px 0;
  }

  .promo__info { // Описание
    padding: 4px 25px 0 0;
    text-align: left;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .promo { // Меняем блок промо
    flex-direction: column;
    align-self: flex-start;

    width: 245px;
    height: 289px;

    margin-left: 70px;
    background-image: url("../img/gift-photo-desktop@1x.jpg");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
      background-image: url("../img/gift-photo-desktop@2x.jpg");
      background-size: 245px auto;
    }
  }

  .promo__image { // значёк Подарок
    padding: 34px 0 0 0;
  }

  .promo__info { // Описание
    padding: 0 0 10px 0;

    text-align: center;
  }
}
