.show-more { // Блок "Показать еще 100500 товаров"
  display: block;

  margin: 26px auto 20px auto;
  padding: 0;

  text-align: center;
  text-decoration: none;

  color: #000000;
}

.show-more__plus {
  width: 30px;
  height: 30px;
}

.show-more__add-more { // Надпись "Показать еще 100500 товаров"
  margin: 0;
  margin-top: 18px;
  padding: 0;

  font-size: 16px;
  line-height: 20px;

  text-transform: uppercase;
}

.show-more__announcement { // Надпись "На самом деле вкусов гораздо больше"
  margin: 0;
  margin-top: 8px;
  padding: 0;

  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;

  text-decoration: none;
}

.show-more__button { // Кнопка "Показать всё"
  width: 100%;
  padding: 10px;

  font-family: inherit;
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;

  background-color: #f2f2f2;
  color: #000000;
}

.show-more__button:hover,
.show-more__button:focus {
  background-color: #ebebeb;
}

.show-more__button:active {
  color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .show-more { // Блок "Показать еще 100500 товаров"
    margin: 0;

    padding-top: 15px;
    padding-left: 3px;
  }

  .show-more__plus {
    width: 60px;
    height: 60px;
  }

  .show-more__add-more { // Надпись "Показать еще 100500 товаров"
    margin-top: 44px;

    width: 170px;

    font-size: 20px;
    line-height: 20px;
  }

  .show-more__announcement { // Надпись "На самом деле вкусов гораздо больше"
    margin-top: 27px;

    width: 170px;

    font-size: 16px;
  }

  .show-more__button { // Кнопка "Показать всё"
    width: 229px;

    margin-top: 11px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .show-more {
    padding: 15px 3px 0 0;
  }

  .show-more__button {
    width: 150px;

    margin-top: 9px;
  }
}
