.work-principle { // Блок "Как это работает"
  margin: 0 auto 24px auto;

  width: 320px;
}

.work-principle__container { // ВНутренний контейнер. Задайем ему отступы
  padding: 0 20px;
}

.work-principle__header { // Заголовок блока
  margin: 0 0 32px 20px;

  font-size: 36px;
  font-weight: 300;
}

.work-principle__list { // Список элементов (сбрасывает отступы и стили)
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  list-style: none;
}

.work-principle__item { // Элемент списка
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 20px;
}

.work-principle__info { // Текст описания "Как это работает"
  display: block;
  margin: 0;
  padding: 0 0 0 20px;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  width: 200px;
}

.work-principle__image { // Картинки
  display: block;

  margin: 0;
  padding: 5px;

  width: 50px;

  background-color: #68b738;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .work-principle {
    width: 708px;
    margin-bottom: 41px;
    padding-left: 20px;
  }

  .work-principle__container { // Отступы у контейнера
    padding: 0;
  }

  .work-principle__list { // Списко карточек
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    width: 708px;
  }

  .work-principle__image { // Картинки
    width: 50px;

    padding: 15px;
  }

  .work-principle__header { // Переопределяем свойсвта заголовка
    margin: 15px 0 0 0;

    font-size: 60px;
    line-height: 60px;
  }

  .work-principle__item { // Элемент списка
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    width: 325px;

    margin: 72px 0 17px 0;
    min-height: 228px;

    &:nth-child(1)::after { // Первой карточке добавляем псевдоэелемент (цифра "1")
      content: "1";

      position: absolute;

      bottom: -15px;
      right: 95px;

      font-size: 281px;
      line-height: 281px;

      color: #f2f2f2;

      z-index: -1;
    }

    &:nth-child(2)::after { // Второй карточке добавляем псевдоэелемент (цифра "2")
      content: "2";

      position: absolute;

      bottom: -15px;
      right: 77px;

      font-size: 281px;
      line-height: 281px;

      color: #f2f2f2;

      z-index: -1;
    }

    &:nth-child(3)::after { // Третьей карточке добавляем псевдоэелемент (цифра "3")
      content: "3";

      position: absolute;

      bottom: -15px;
      right: 80px;

      font-size: 281px;
      line-height: 281px;

      color: #f2f2f2;

      z-index: -1;
    }

    &:nth-child(4)::after { // Четвёртой карточке добавляем псевдоэелемент (цифра "4")
      content: "4";

      position: absolute;

      bottom: -15px;
      right: 77px;

      font-size: 281px;
      line-height: 281px;

      color: #f2f2f2;

      z-index: -1;
    }
  }

  .work-principle__info { // блок описания
    width: 245px;

    margin-top: 33px;
    padding: 0;

    font-size: 16px;
    line-height: 24px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .work-principle {
    width: 100%;

    padding: 0;
  }

  .work-principle__container {
    width: 1220px;

    margin: 0 auto;
    padding: 0 0 0 20px;
  }

  .work-principle__header {
    box-sizing: border-box;

    width: 1220px;

    margin: 44px auto 0 auto;
    padding-left: 10px;
  }

  .work-principle__list {
    width: 1220px;
  }

  .work-principle__item {
    width: 245px;
    margin-top: 71px;

    &:nth-child(1)::after { // Переопределяем положение псевдоэлемента (цифра "1")
      right: 15px;
    }

    &:nth-child(2)::after { // Переопределяем положение псевдоэлемента (цифра "2")
      right: 0;
    }

    &:nth-child(3)::after { // Переопределяем положение псевдоэлемента (цифра "3")
      right: 0;
    }

    &:nth-child(4)::after { // Переопределяем положение псевдоэлемента (цифра "4")
      right: -5px;
    }
  }
}
