.map { // Блок с картой
  margin: 0 auto;
  padding: 0;

  width: 100%;
  height: 362px;

  background-color: #f2f2f2;
  background-image: url("../img/bg-map-mobile@1x.png");
  background-position: bottom;
  background-repeat: no-repeat;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
    background-image: url("../img/bg-map-mobile@2x.png");
    background-size: 320px auto;
  }
}

.map__wrapper { // Обёртка для значка на карте. Без неё не удаёться спозиционировать маркер на карте
  position: relative;
  width: 280px;
  margin: 0 auto;
}

.map__yandex { // iframe с яндекса
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 362px;
  border: none;
}

.map__yandex--hide { // Яндекс карта изначально скрыта
  display: none;
}

.map__pin-image { // Маркер
  position: absolute;
  top: 113px;
  left: 78px;

  transform: scale(0.5);
}

.map__pin-image--js { // Скрываем маркер при работающем скрипте
  display: none;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .map { // Меняем размер блока
    // width: 767px;
    height: 400px;

    background-image: url("../img/bg-map-tablet@1x.png");
    background-position: top;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
      background-image: url("../img/bg-map-tablet@2x.png");
      background-size: 767px auto;
    }
  }

  .map__yandex {
    height: 400px;
  }

  .map__pin-image {
    top: 150px;
    left: 80px;

    transform: scale(1);
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .map { // Меняем размер блока
    width: 100%;
    background-image: url("../img/bg-map-desktop@1x.png");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
      background-image: url("../img/bg-map-desktop@2x.png");
      background-size: 1440px auto;
    }
  }

  .map__pin-image {
    top: 150px;
    left: 418px;

    transform: scale(1);
  }
}
