.page-main {
  width: 100%;
  margin: 0 auto;
}

.page-main__header { // Заголовки h1
  width: 280px;

  margin: 0 auto 28px auto;
  padding: 0 20px;

  font-size: 36px;
  line-height: 60px;
  font-weight: 300;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .page-main__header { // Переопределяем размеры заголовка
    width: 708px;

    margin: 0 auto 26px auto;
    padding: 0 0 0 20px;

    font-size: 60px;
    line-height: 60px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->

  .page-main__header { // Переопределяем размеры заголовка
    width: 1220px;

    margin: 0 auto 27px auto;

    padding: 0 0 0 20px;
  }
}
