.page-footer { // Футер
  width: 100%;

  background-color: #f2f2f2;
}

.page-footer__container { // Контейнер для содержимого футера
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  width: 280px;

  margin: 0 auto;

  padding: 0 20px;
}

.page-footer__logo { // Ссылка логотипа
  display: block;

  height: 23px;

  padding: 37px 0 33px 0;
}

.page-footer__logo-image {
  display: block;
  width: 132px;
}

.page-footer__logo-image:hover,
.page-footer__logo-image:focus {
  opacity: 0.8;
}

.page-footer__logo-image:active {
  opacity: 0.6;
}

.page-footer__copyright { // Блок "Copyright"
  margin-top: 14px;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .page-footer { // Меняем размер футера
    width: 100%;
  }

  .page-footer__container { // Меняем отступы контейнера
    flex-direction: row;

    width: 708px;

    margin: 0 auto;
    padding: 45px 0 50px 20px;
  }

  .page-footer__logo { // Центровка Картинки Cat Energy
    margin: 0;
    padding: 0;
  }

  .page-footer__logo-image {
    width: 129px;
    height: 24px;
  }

  .page-footer__copyright { // Блок "Copyright"
    margin: 0;
    padding: 0;
    width: 163px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->

  .page-footer__container {  // Размеры и отступы контейнера
    width: 1220px;

    margin: 0 auto;

    padding: 49px 0 49px 0;
  }

  .page-footer__copyright {
    width: 170px;
    margin-right: -15px;
  }

  .page-footer__logo {
    margin-left: 8px;
  }
}
