.page-header { // Хедер сайта
  margin: 0 auto 15px auto;
  padding: 0;

  width: 100%;
  min-height: 65px;
}

.page-header--border {
  border-bottom: 1px solid #e6e6e6;
}

.page-header__container { // Обёртка для логотипа и кнопки открытия меню
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  width: 320px;

  margin: 0 auto;
  padding-top: 12px;
}

.page-header__logo { // Размер логотипа
  width: 190px;
  margin-left: 20px;
}

.page-header__image {
  display: block;
}

.page-header__toggle {
  margin-right: 20px;
}

.page-header__toggle--opened { // Кнопка "Бургер" главного меню
  width: 24px;
  height: 18px;

  list-style: none;
  font-size: 0;
  border: none;

  background: transparent url("../img/icon-mobile-menu.svg") 0 0 no-repeat;
}

.page-header__toggle--closed {
  width: 21px;
  height: 18px;

  list-style: none;
  font-size: 0;
  border: none;

  background: transparent url("../img/icon-close.svg") 0 0 no-repeat;
}

.page-header__toggle--nojs {
  display: none;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .page-header {
    min-height: 742px;

    margin-top: 11px;
  }

  .page-header--border {
    border: none;
  }

  .page-header__container {
    align-items: flex-start;

    width: 708px;

    padding: 13px 0 0 20px;

    margin: 10px auto 60px auto;
  }

  .page-header__logo { // Меняем картинку на другую (добавим потом)
    margin: 0;
    width: 173px;
  }

  .page-header__toggle { // Скрываем кнопку меню
    display: none;
  }

  .page-header--clear { // Нужен для страниц каталога и формы
    min-height: 60px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .page-header {
    margin: 0 auto;

    min-height: 696px;
  }

  .page-header--background::after { // Псевдоэлемент для заголовка с фоновым изображением кота
    content: "";

    position: absolute;

    top: 0;
    right: 0;

    width: calc(50% - 9px); // Вычисляем ширину согласно макету
    height: 696px;

    background-color: #65a93c;
    background-image: url("../img/index-background-desktop@2x.jpg");
    background-repeat: no-repeat;
    background-position: 0%;
    background-size: cover;

    z-index: -1;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {  // "Нежная" ретинизация
      background-color: #65a93c;
      background-image: url("../img/index-background-desktop@2x.jpg");
      background-repeat: no-repeat;
      background-position: 0%;
      background-size: cover;
    }
  }

  .page-header__container { // Меняем размер контейнера
    width: 1220px;

    margin: 0 auto;

    padding: 54px 0 0 20px;
  }

  .page-header__logo { // Меняем картинку на другую (добавим потом)
    width: 202px;
  }

  .page-header__image:hover { // Реакция при наведении на логотип
    opacity: 0.8;
  }

  .page-header__image:active { // Реакция при нажатии на логотип
    opacity: 0.6;
  }

  .page-header--clear { // Нужен для страниц каталога и формы
    min-height: 125px;

    margin-bottom: 63px;

    color: #ffffff;
  }
}
