.offer { // Блок "Функциональное питание для котов"
  position: relative;

  margin: 0 auto 197px auto;

  width: 100%;

  background: url("../img/index-background-mobile@1x.jpg") top no-repeat;

  color: #ffffff;

  background-color: #65a93c;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) { // «Нежная» ретинизация
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 320px auto;
  }
}

.offer__container {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  width: 320px;

  margin: 0 auto;
  padding-bottom: 138px;
}

.offer__header { // Заголовок блока
  margin: 0 auto;
  padding: 0;

  margin-top: 27px;

  width: 280px;

  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-align: center;
}

.offer__slogan { // Слоган блока
  margin: 26px auto 28px auto;

  font-size: 14px;
  line-height: 14px;
  font-weight: 400;

  text-align: center;
  text-transform: uppercase;
}

.offer__product-image { // Изображение Cat Energy
  display: block;
  position: absolute;

  left: 20px;
}

.offer__button { // Кнопка блока
  position: absolute;
  display: block;

  width: 280px;

  right: 20px;
  bottom: -175px;


  padding: 10px 0;

  font-size: 14px;
  line-height: 18px;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .offer { // Весь блок становится шириной 768px
    background: none;
  }

  .offer__container { // Делаем отступы для контейнера
    width: 708px;
    padding: 0;
  }

  .offer__header { // Выставляем размеры и шрифт заголовка блока
    width: 427px;
    margin: 0 0 41px 97px;

    font-size: 60px;
    line-height: 60px;

    text-align: left;

    color: #000000;
  }

  .offer__slogan { // Выставляем размеры и шрифт описания блока
    font-size: 20px;
    line-height: 20px;

    margin: 0 0 0 98px;

    text-align: left;

    color: #000000;
  }

  .offer__product-image { // Позиционируем картинку абсолютно.
    position: absolute;

    top: 200px; // Временно, т.к. картинка меняеться на планшетную
    left: 9px;

    z-index: -1; // картинка "уходит" под кнопку
  }

  .offer__button {
    left: 99px;
    bottom: -98px;

    font-size: 20px;
    line-height: 20px;
    width: 245px;

    padding: 16px 0;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .offer {
    width: 100%;  // Меняем ширину всего блока
    margin-bottom: 80px;
    margin-top: 109px;
  }

  .offer__header {
    margin: 0;
    padding: 0;

    margin-bottom: 40px;
  }

  .offer__container {
    box-sizing: border-box;
    width: 1220px;
    margin: 0 auto;
    padding: 0 40px 0 90px;
  }

  .offer__slogan {
    margin: 0;
  }

  .offer__product-image { // Позиционируем картинку абсолютно.
    top: -80px;
    left: 460px;

    z-index: 1;
  }

  .offer__button {
    left: 87px;
    bottom: -105px;
  }
}
