.catalog { // Блок товаров
  margin: 0 auto;

  width: 320px;
}

.catalog__list { // Убирает лишние отступы и стили у списка
  margin: 0;
  padding: 0;

  list-style: none;

  display: flex;
  flex-direction: column;
}

.catalog__item { // Добавляем обводку к карточке
  border: 1px solid #ededed;
  border-bottom: none;
}

.catalog__item:last-child { // Отступ и граница у последней карточки
  padding-bottom: 25px;

  border-bottom: 1px solid #ededed;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .catalog { // Меняем размер блока
    width: 708px;

    margin-bottom: 35px;

    padding-left: 20px;
  }

  .catalog__list { // Меняем на направление флекс элементов + отступы
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    margin: 0;
    padding: 0;
  }

  .catalog__item {
    border: none;
  }

  .catalog__item:last-child {
    padding: 0;
    border: none;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .catalog { // Размер на весь экран
    width: 100%;

    margin-bottom: 40px;

    padding: 0;
  }

  .catalog__list { // Размер списка + отступы
    width: 1220px;

    margin: 0 auto;

    padding: 0 0 0 20px;
  }

  .catalog__item { // Размеры карточек
    width: 245px;
  }
}
