@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
body {
  box-sizing: border-box;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}
.button {
  display: block;
  font-size: 14px;
  line-height: 14px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  border: none;
  background-color: #68b738;
  color: #ffffff;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
.addition-header {
  position: relative;
  margin: 0 0 28px 0;
  padding: 0;
  font-size: 24px;
  line-height: 24px;
}
.addition-header::after {
  content: "";
  position: absolute;
  top: 13px;
  right: -159px;
  width: 136px;
  height: 3px;
  background-color: #68b738;
}
.addition__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  width: 280px;
}
.addition__item {
  margin: 0;
  padding: 9px 0 9px 46px;
  width: 280px;
  text-transform: uppercase;
}
.addition__item label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  width: max-content;
}
.addition__checkbox + label::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -46px;
  width: 29px;
  height: 29px;
  border: 1px solid #dedede;
}
.addition__checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 2px;
  left: -40px;
  width: 18px;
  height: 10px;
  border-left: 2px solid #8dc968;
  border-bottom: 2px solid #8dc968;
  transform: rotate(-45deg);
  background-color: #ffffff;
}
.addition__checkbox:disabled + label::before {
  opacity: 0.1;
}
.addition__checkbox:disabled + label {
  opacity: 0.3;
}
.addition__checkbox:hover + label::before,
.addition__checkbox:focus + label::before {
  border-color: #bebebe;
}
.addition__checkbox:active + label::before {
  opacity: 0.3;
}
.addition__checkbox:active + label {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .addition-header {
    margin: 0 0 51px 0;
    font-size: 36px;
    line-height: 36px;
  }
  .addition-header::before {
    content: "";
    position: absolute;
    top: 22px;
    left: -70px;
    width: 30px;
    height: 3px;
    background-color: #68b738;
  }
  .addition-header::after {
    top: 22px;
    left: 253px;
    width: 425px;
    height: 3px;
  }
  .addition__list {
    width: 628px;
  }
  .addition__item {
    box-sizing: border-box;
    margin-bottom: 27px;
    padding: 15px 0 10px 60px;
    width: 285px;
    font-size: 20px;
    line-height: 20px;
  }
  .addition__checkbox + label::before {
    top: -9px;
    left: -60px;
    width: 38px;
    height: 38px;
  }
  .addition__checkbox:checked + label::after {
    top: 2px;
    left: -50px;
  }
}
@media (min-width: 1300px) {
  .addition-header::before {
    width: 70px;
    left: -109px;
  }
  .addition-header::after {
    top: 23px;
    left: 250px;
    width: 990px;
  }
  .addition__list {
    width: 1220px;
  }
  .addition__item {
    width: 245px;
  }
}
.before-after {
  box-sizing: border-box;
  width: 100%;
}
.before-after__cat--fat {
  display: block;
  width: 320px;
  height: 240px;
  margin: 0 auto;
}
.before-after__cat--slim {
  display: none;
}
.before-after__range-control {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  margin: 32px 0 36px 5px;
}
.before-after__scale {
  flex-grow: 0;
  flex-shrink: 0;
  width: 83px;
  height: 12px;
  margin: 0 17px;
  border-radius: 6px;
  border: 1px solid #cdcdcd;
  background-color: #ffffff;
}
.before-after__bar {
  width: 42%;
  height: 6px;
  margin: 3px 0 1px 6px;
  border-radius: 10px;
  background-color: #68b738;
}
.before-after__text {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.before-after__range-toggle {
  display: none;
  position: absolute;
  top: 54px;
  left: 125px;
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 50%;
  font-size: 0;
  background-color: #ffffff;
  box-shadow: 0 1px 3px 2px #cdcdcd;
}
.before-after__range-toggle::after {
  content: "";
  position: absolute;
  top: 12px;
  left: 12px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #68b738;
}
.before-after__range-toggle:hover,
.before-after__range-toggle:focus {
  box-shadow: 0 2px 4px 3px #cdcdcd;
}
.before-after__range-toggle:active {
  border: 2px solid #68b738;
}
.before-after__range-toggle:active::after {
  top: 10px;
  left: 10px;
}
@media (min-width: 768px) {
  .before-after {
    position: relative;
    width: 100%;
    margin-top: 221px;
    padding: 70px 0 66px 0;
    background-color: #f2f2f2;
    z-index: -3;
  }
  .before-after::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: calc(50% - 10px);
    height: 462px;
    background-color: #eaeaea;
    z-index: -2;
  }
  .before-after__container {
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;
    width: 708px;
    height: 327px;
    min-height: 327px;
  }
  .before-after__image {
    position: absolute;
    border: none;
  }
  .before-after__image--before {
    top: -239px;
    left: 26px;
    width: 338px;
    min-height: 632px;
    overflow: hidden;
    z-index: 1;
    background-color: #f2f2f2;
    background-image: linear-gradient(180deg, #ffffff 169px, #f2f2f2 169px);
  }
  .before-after__image--after {
    top: -239px;
    left: 26px;
    min-height: 632px;
    background-color: #eaeaea;
    background-image: linear-gradient(180deg, #ffffff 169px, #eaeaea 169px);
  }
  .before-after__cat--fat {
    width: 690px;
    max-width: 690px;
    height: 517px;
    margin: 0;
  }
  .before-after__cat--slim {
    display: block;
    width: 690px;
    max-width: 690px;
    height: 517px;
    margin: 0;
  }
  .before-after__range-control {
    width: 570px;
    margin: 14px auto 4px auto;
    padding-left: 20px;
    z-index: 1;
  }
  .before-after__scale {
    width: 430px;
    margin: 0 20px 0 20px;
    background-color: transparent;
    border: none;
  }
  .before-after__bar {
    width: 100%;
    margin: 3px 0 0 0;
    background-color: #dcdcdc;
  }
  .before-after__range-toggle {
    display: block;
    top: 0;
    left: 289px;
  }
  .before-after__text {
    padding: 5px 0;
    font-size: 20px;
    border-bottom: 1px dotted #bebebe;
  }
  .before-after__text:hover,
  .before-after__text:focus {
    border-bottom: 1px solid #bebebe;
  }
  .before-after__text:active {
    border-color: transparent;
  }
}
@media (min-width: 1300px) {
  .before-after {
    position: inherit;
    width: 693px;
    margin: 0;
    z-index: 0;
  }
  .before-after::after {
    width: calc(50% - 335px);
    z-index: -1;
  }
  .before-after__container {
    justify-content: flex-end;
    width: 690px;
