  }
  .before-after__image--after {
    width: 652px;
    min-height: 652px;
    top: -259px;
    left: 40px;
    background-image: linear-gradient(180deg, #ffffff 189px, #eaeaea 189px);
    overflow: hidden;
  }
  .before-after__range-control {
    margin: 0 0 3px 0;
    padding: 0;
  }
  .before-after__range-toggle {
    left: 269px;
  }
}
