.program { // Блока подбора программы (form.html)
  margin: 0 auto 33px auto;

  width: 320px;
}

.program__request { // Всю форму делаем флекс-контейнером (пригодилось на десктопной версии)
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.program__container { // Общие отступы (20px) по макету
  padding: 0 20px;
}

.program__header { // Общий заголовок страницы
  margin: 0 0 25px 0;
  padding: 0 20px 0 20px;

  width: 280px;

  font-size: 36px;
  line-height: 60px;
  font-weight: 300;
}

.program__description { // Заполните анкету и мы подберём....
  margin: 0 0 25px 0;
  padding: 0 20px 0 20px;

  width: 230px; // Примерная ширина на мобильном макете (либо юзать br)

  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.program__cat { // Блок "О коте" (Имя, вес возраст)
  margin: 0;
  padding: 0;

  width: 280px;

  list-style: none;

  border: none;
}

.program__cat-info { // input + label В блоке "О коте"
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: baseline;

  margin: 15px 0 17px 0;
  padding: 0;

  text-transform: uppercase;
}

.program__cat-info input {
  flex-grow: 1;

  width: auto;

  padding: 11px 11px 11px 14px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  background-color: #ffffff;

  color: #000000;

  border: 1px solid #e6e6e6;
}

.program__cat-info input:hover { // Изменение прозрачности поля ввода при наведении
  border-color: #cdcdcd;
}

.program__cat-info input:active,
.program__cat-info input:focus { // Изменение прозрачности поля ввода при нажатии(фокусе)
  outline: none;

  border-color: #000000;

  box-shadow: inset 0 0 0 1px #000000;
}

.program__cat-info input:invalid { // Изменение стилей при неправильном вводе (Работает НЕ правильно!). Скорее всего нужен js
  border-color: #ff8282;

  box-shadow: inset 0 0 0 1px #ff8282;
}


.program__cat-info label { // Корректно или надо через классы?
  margin-right: 15px;
}

.program__type { // Блок "Тип программы" (Похудение и т.д.)
  margin: 8px 0 28px 0;
  padding: 13px 0 11px 0;

  width: 320px;

  list-style: none;

  border: 1px solid #ededed;
}

.program__list { // Список в блоке "Тип программы"
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  width: 280px;

  margin: 0;
  padding: 0;

  list-style: none;
}

.program__item { // Элементы списка в блоке "Тип программы"
  margin: 0;
  padding: 10px 0 10px 65px;

  width: 280px;

  text-transform: uppercase;
}

.program__item label { // Блок с подписью
  position: relative;

  display: block;

  cursor: pointer;
  user-select: none;

  width: max-content; // Убирает баг, когда, при нажатии на радио кнобку, подсвечивается вся строка.
}

.program__radio + label::before { // Псевдоэлемент для костомизации радиокнопки
  content: "";

  position: absolute;

  top: -3px;
  left: -45px;

  width: 27px;
  height: 27px;

  border: 1px solid #cecece;

  border-radius: 50%;
}

.program__radio:checked + label::after { // Псевдоэлемент для костомизации радиокнопки
  content: "";

  position: absolute;

  top: 5px;
  left: -36px;

  width: 12px;
  height: 12px;

  border-radius: 50%;

  background-color: #68b738;
}

.program__radio:disabled + label::before { // Внешний вид при отключенной радио-кнопке
  opacity: 0.1;
}

.program__radio:disabled + label { // Внешний вид надписи при отключенной радио-кнопке
  opacity: 0.3;
}

.program__radio:hover + label::before,
.program__radio:focus + label::before { // Реакция радио кнопки при фокусе и наведении
  border-color: #bebebe;
}

.program__radio:active + label::before { // Реакция радио кнопки при нажатии
  opacity: 0.3;
}

.program__radio:active + label { // Реакция надписи при нажатии на радио кнопку
  opacity: 0.3;
}

.program__owner { // Блок "Владелец"
  margin: 0 0 28px 0;
  padding: 0;

  list-style: none;

  border: none;
}

.program__owner-header { // Заголовок блока "Владелец"
  position: relative;

  width: 280px;

  margin: 0 0 19px 0;
  padding: 0;

  font-size: 24px;
  line-height: 24px;
}

.program__owner-header::after { // Декоративная зелёная полоска блока "Владелец"
  content: "";

  position: absolute;

  top: 13px;
  right: -20px;

  width: 100px;
  height: 3px;

  background-color: #68b738;
}

.program__owner-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  width: 280px;
}

.program__owner-info { // input + label В блоке "О коте"
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: baseline;
  flex-wrap: nowrap;

  margin: 17px 0 0 0;
  padding: 0;

  width: 280px;

  text-transform: uppercase;
}

.program__owner-info input { // Корректно или надо через классы?
  flex-grow: 1;

  width: auto;

  padding: 11px 35px 11px 12px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  background-color: #ffffff;

  color: #000000;

  border: 1px solid #e6e6e6;
}

.program__owner-info input:hover { // Изменение прозрачности поля ввода при наведении
  border-color: #cdcdcd;
}

.program__owner-info input:active,
.program__owner-info input:focus { // Изменение прозрачности поля ввода при нажатии(фокусе)
  outline: none;

  border-color: #000000;

  box-shadow: inset 0 0 0 1px #000000;
}

.program__owner-info input:invalid { // Изменение стилей при неправильном вводе (Работает НЕ правильно!). Скорее всего нужен js
  border-color: #ff8282;

  box-shadow: inset 0 0 0 1px #ff8282;
}

.program__owner-input:active + svg,
.program__owner-input:focus + svg { // Изменение цвета иконок при нажатии(фокусе)
  fill: #68b738;
}

.program__owner-input:invalid + svg { // Изменение стилей при неправильном вводе (Работает НЕ правильно!). Скорее всего нужен js
  fill: #ff8282;
}

.program__owner-info label { // Корректно или надо через классы?
  margin-right: 13px;
}

.program__owner-image { // Картинки с телефоном и конвертом
  position: absolute;

  top: 16px;
  right: 15px;
}

.program__owner-image--phone { // Позиционирование картинка с телефоном
  top: 12px;
  right: -5px;
}

.program__comment { // Блок "Комментарий"
  margin: 0 0 25px 0;
  padding: 0;

  list-style: none;

  border: none;
}

.program__comment-header { // Заголовок блока "Комментарий"
  position: relative;

  margin: 0 0 35px 0;
  padding: 0;

  font-size: 24px;
  line-height: 24px;
}

.program__comment-header::after { // Декоративная зелёная полоска блока "Комментарий"
  content: "";

  position: absolute;

  top: 15px;
  right: -180px;

  width: 158px;
  height: 3px;

  background-color: #68b738;
}


.program__comment textarea { // Поле ввода комментария для блока "Комментарий"
  box-sizing: border-box;

  margin: 0;
  padding: 13px 10px 0 15px;

  width: 280px;
  height: 150px;

  resize: none;
  overflow: auto;

  border: 1px solid #ededed;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  background-color: #ffffff;

  color: #000000;
}

.program__comment textarea:hover { // Изменение прозрачности поля ввода при наведении
  border-color: #cdcdcd;
}

.program__comment textarea:active,
.program__comment textarea:focus { // Изменение прозрачности поля ввода при нажатии(фокусе)
  outline: none;

  border-color: #000000;

  box-shadow: inset 0 0 0 1px #000000;
}

.program__addition { // Блок "Дополнительно"
  margin: 0 0 30px 0;
  padding: 0;

  list-style: none;

  border: none;
}

.program__wrapper { // Обёртка для позиционирования кнопки и сноски
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  width: 280px;
}

.program__send-button { // Кнопка
  display: block;

  width: 280px;

  padding: 10px 0 10px 0;
  margin-bottom: 19px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;

  text-decoration: none;
  text-align: center;
  text-transform: uppercase;

  border: none;

  background-color: #68b738;
  color: #ffffff;
}

.program__notice { // Обозначения (* -- Обязательное поле)
  display: block;

  margin: 0 auto;

  width: 280px;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;

  text-align: center;
}

