.extra-goods { // Блок "Дополнительные товары" на странице каталога
  margin: 0 auto 34px auto;
  padding: 0;

  width: 320px;
}

.extra-goods__header { // Заголовок
  position: relative;
  margin: 0;
  padding: 28px 20px 35px 20px;

  font-size: 24px;
  line-height: 24px;
  font-weight: 300;
}

.extra-goods__header::after { // Зелёная полоска справа от хедера
  content: "";

  position: absolute;

  top: 42px;
  right: 0;

  width: 50px;
  height: 3px;

  background-color: #68b738;
}

.extra-goods__container { // Контейнер для содержимого блока (без заголовка)
  padding: 0 20px;
}

.extra-goods__list { // Список товаров
  margin: 0 0 38px 0;
  padding: 0;

  list-style: none;
}

.extra-goods__item { // Блок с товаром
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  padding: 10px 0 17px 0;

  border-top: 1px solid #cdcdcd;
}

.extra-goods__item:last-child { // Задаём последнему товару границу снизу
  border-bottom: 1px solid #cdcdcd;
}

.extra-goods__name { // Название товара
  margin: 0;
  padding: 3px 0;

  width: 280px;

  font-size: 16px;
  line-height: 16px;
  font-weight: 300;
  text-transform: uppercase;
}

.extra-goods__info { // Блок обёртка для описания товара и его стоимости
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  flex-wrap: wrap;

  width: 280px;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
}

.extra-goods__description { // Описание товара
  margin: 0;
  padding: 10px 0 10px 0;

  margin-bottom: 5px;
}

.extra-goods__price { // Цена товара
  margin: 0;
  padding: 0;

  text-transform: uppercase;
}

.extra-goods__button { // Кнопка заказать
  width: 280px;

  padding: 10px 0 10px 0;
  font-size: 16px;
  line-height: 20px;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .extra-goods {
    width: 708px;
    margin-bottom: 76px;
    padding-left: 20px;
  }

  .extra-goods__header { // Меняем хедер
    padding: 30px 0 80px 37px;

    font-size: 36px;
    line-height: 36px;
  }

  .extra-goods__header::before { // Зелёная полоска слева от хедера
    content: "";

    position: absolute;

    top: 52px;
    left: -30px;

    width: 30px;
    height: 3px;

    background-color: #68b738;
  }

  .extra-goods__header::after { // Зелёная полоска справа от хедера
    top: 52px;
    right: -11px;

    width: 307px;
    height: 3px;
  }

  .extra-goods__container { // Меняем отступы у контейнера
    padding: 0;
  }

  .extra-goods__list { // Список
    margin: 0 0 69px 0;
  }

  .extra-goods__item { // Карточка товара
    align-items: baseline;

    padding: 20px 36px 27px 38px;

    border: 1px solid #cdcdcd;
    border-bottom: none;
  }

  .extra-goods__name { // Навзание товара
    font-size: 20px;
    line-height: 20px;
  }

  .extra-goods__info { // Блок описания + цены товара
    justify-content: flex-end;

    font-size: 16px;
    line-height: 20px;
  }

  .extra-goods__price { // Цена товара
    width: 50px;

    margin-left: 45px;

    text-align: right;
  }

  .extra-goods__button { // Кнопка Заказать
    width: 100%;

    margin-top: 6px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .extra-goods { // Меняем размеры блока
    width: 1220px;
    margin-bottom: 18px;
  }

  .extra-goods__header { // Отступы у заголовка
    padding: 30px 0 80px 0;
  }

  .extra-goods__header::before { // Зелёная полоска слева от хедера
    content: "";

    position: absolute;

    top: 52px;
    left: -70px;

    width: 30px;
    height: 3px;

    background-color: #68b738;
  }

  .extra-goods__header::after { // Зелёная полоска справа от хедера
    content: "";

    position: absolute;

    top: 53px;
    right: -20px;

    width: 867px;
    height: 3px;

    background-color: #68b738;
  }

  .extra-goods__container { // Делаем флекс контейнер
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    width: 1220px;

    padding: 0;
  }

  .extra-goods__list { // Список
    width: 895px;
  }

  .extra-goods__item { // Карточка товара
    padding: 17px 0 7px 0;

    border-left: none;
    border-right: none;
  }

  .extra-goods__name { // Название товара
    width: 285px;
  }

  .extra-goods__info { // Блок описания + цены товара
    justify-content: space-between;

    width: 294px;

    margin-left: auto;
    padding-right: 30px;
  }

  .extra-goods__price { // Цена товара
    text-align: left;
  }

  .extra-goods__button { // Кнопка Заказать
    width: 245px;

    margin: 0;
  }
}
