.social { // Блок социальных сетей
  margin: 0;
  padding: 4px 0 3px 0;

  width: 100%;

  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.social__list { // Списко (<ul>)
  margin: 0;
  padding: 0;

  list-style: none;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.social__link { // центруем значки внутри блока
  display: flex;
  justify-content: center;
  align-items: center;

  height: 55px;
  width: 55px;
}

.social__image {
  fill: #666666;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .social {
    margin: 0 0 0 30px;
    padding: 0;

    width: 170px;

    border: none;
  }

  .social__list {
    width: 150px;
    margin: 0 auto;
    padding: 0;
  }

  .social__link {
    width: 50px;
    height: 51px;
  }

  .social__item:nth-child(3) {
    box-sizing: border-box;
    padding-right: 5px;
  }

  .social__image {
    transform: scale(1.4);
  }

  .social__image:hover,
  .social__image:focus { // Меняем цвет при наведении на иконку соц. сети
    fill: #68b738;
  }

  .social__image:active { // Меняем прозрачность при нажатии на иконку соц. сети
    opacity: 0.5;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .social {
    width: 180px;

    margin: 0 0 0 40px;
  }
}
