.contacts { // Блок контактов
  position: relative;

  width: 100%;

  margin: 0 auto;

  background-color: #ffffff;
}

.contacts--background {
  background-color: #f2f2f2;
}

.contacts__container { // Контейнер для текстовой информации
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 280px;

  margin: 0 auto;
  padding: 30px 20px 26px 20px;

  background-color: #ffffff;
}

.contacts__container--color { // Контейнер для текстовой информации
  background-color: #f2f2f2;
}

.contacts__message { // Приглашаем к сотрудничеству дилеров!
  margin: 0;
  padding: 0;

  width: 140px;

  font-size: 16px;
  line-height: 20px;

  text-transform: uppercase;
}

.contacts__adress { //ул. Большая Конюшенная, д. 19/8 Санкт-Петербург
  margin: 0;
  padding: 0;

  width: 140px;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;

  text-align: left;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .contacts { // Меняем размер блока
    padding: 0;
  }

  .contacts__container { // Меняем отступы
    align-items: center;

    width: 708px;

    padding: 62px 0 58px 0;
  }

  .contacts__message { // Меняем размер блока
    width: 160px;

    margin-left: 51px;

    font-size: 20px;
    line-height: 25px;
  }

  .contacts__adress { // Меняем размер блока
    width: 160px;

    margin-right: 31px;

    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }

  .contacts__adress span {
    display: block;
    padding-top: 20px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .contacts { // Делаем блока во всю ширину
    width: 100%;
  }

  .contacts__wrapper {
    position: relative;

    width: 1220px;

    margin: 0 auto;
  }

  .contacts__container { // Вырываем блок из потока и позиционируем абсолютно
    box-sizing: border-box;

    position: absolute;

    align-items: center;

    width: 570px;
    height: 200px;

    top: 95px;
    left: 11px;

    z-index: 1;

    background-color: #ffffff;

    padding: 85px 75px 80px 75px;
  }

  .contacts__message {
    margin: 0;
  }

  .contacts__adress { // Вырываем блок из потока и позиционируем абсолютно
    text-align: left;
    margin-right: 10px;
  }
}
