.addition-header { // Заголовок блока "Дополнительно"
  position: relative;

  margin: 0 0 28px 0;
  padding: 0;

  font-size: 24px;
  line-height: 24px;
}

.addition-header::after { // Декоративная зелёная полоска блока "Дополнительно"
  content: "";

  position: absolute;

  top: 13px;
  right: -159px;

  width: 136px;
  height: 3px;

  background-color: #68b738;
}

.addition__list { // Сброс стилей у списка дополнительных товаров
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  list-style: none;

  width: 280px;
}

.addition__item { //Элемент списка дополнительных товаров
  margin: 0;
  padding: 9px 0 9px 46px;

  width: 280px;

  text-transform: uppercase;
}

.addition__item label { // Блок с подписью
  position: relative;

  display: block;

  cursor: pointer;
  user-select: none;

  width: max-content;  // Убирает баг, когда, при нажатии на радио кнобку, подсвечивается вся строка.
}

.addition__checkbox + label::before { // Псевдоэлемент для костомизации чекбокса
  content: "";

  position: absolute;

  top: -3px;
  left: -46px;

  width: 29px;
  height: 29px;

  border: 1px solid #dedede;
}

.addition__checkbox:checked + label::after { // Псевдоэлемент (Галочка) для костомизации чекбокса
  content: "";

  position: absolute;

  top: 2px;
  left: -40px;

  width: 18px;
  height: 10px;


  border-left: 2px solid #8dc968;
  border-bottom: 2px solid #8dc968;

  transform: rotate(-45deg);

  background-color: #ffffff;
}

.addition__checkbox:disabled + label::before { // Внешний вид при отключенном чекбоксе
  opacity: 0.1;
}

.addition__checkbox:disabled + label { // Внешний вид надписи при отключенном чекбоксе
  opacity: 0.3;
}

.addition__checkbox:hover + label::before,
.addition__checkbox:focus + label::before { // Реакция чекбокса при фокусе и наведении
  border-color: #bebebe;
}

.addition__checkbox:active + label::before { // Реакция чекбокса при нажатии
  opacity: 0.3;
}

.addition__checkbox:active + label { // Реакция надписи при нажатии на чекбокс
  opacity: 0.3;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .addition-header { // Заголовок блока "Дополнительно"
    margin: 0 0 51px 0;

    font-size: 36px;
    line-height: 36px;
  }

  .addition-header::before { // Декоративная зелёная полоска блока "Дополнительно"
    content: "";

    position: absolute;

    top: 22px;
    left: -70px;

    width: 30px;
    height: 3px;

    background-color: #68b738;
  }

  .addition-header::after { // Декоративная зелёная полоска блока "Дополнительно"
    top: 22px;
    left: 253px;

    width: 425px;
    height: 3px;
  }

  .addition__list {
    width: 628px;
  }

  .addition__item { //Элемент списка дополнительных товаров
    box-sizing: border-box;

    margin-bottom: 27px;
    padding: 15px 0 10px 60px;

    width: 285px;

    font-size: 20px;
    line-height: 20px;
  }

  .addition__checkbox + label::before { // Псевдоэлемент для костомизации чекбокса
    top: -9px;
    left: -60px;

    width: 38px;
    height: 38px;
  }

  .addition__checkbox:checked + label::after { // Псевдоэлемент (Галочка) для костомизации чекбокса
    top: 2px;
    left: -50px;
  }
}

@media (min-width: 1300px) { // Если экран больше 1300px ----->
  .addition-header::before { // Декоративная зелёная полоска блока "Дополнительно"
    width: 70px;

    left: -109px;
  }

  .addition-header::after { // Декоративная зелёная полоска блока "Дополнительно"
    top: 23px;
    left: 250px;

    width: 990px;
  }

  .addition__list {
    width: 1220px;
  }

  .addition__item {
    width: 245px;
  }
}
