.example { // Блок "Живой пример"
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;

  width: 100%;

  margin: 0 auto;
  padding: 0;

  background-color: #f2f2f2;
}

.example__wrapper {
  margin: 0 auto;
}

.example__container { // Контейнер для описания блока
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;

  width: 320px;

  margin: 0 auto;
}

.example__header { // Заголовок блока
  margin: 16px 0 24px 0;
  padding: 0 0 0 20px;

  width: 280px;

  font-size: 36px;
  line-height: 60px;
  font-weight: 300;
}

.example__info { // "Борис сбросил 5 кг...."
  margin: 0;
  margin-bottom: 25px;

  padding: 0 30px 0 20px;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.example__stats { // Список характеристик (5 кг, 60 дней)
  display: flex;
  justify-content: space-between;

  list-style: none;

  width: 280px;

  margin: 0;
  margin-bottom: 34px;
  padding: 0 0 0 20px;
}

.example__item { // "5 кг снижение веса, 60 дней затрацено времени"
  position: relative;

  width: 122px;

  padding-top: 15px;
  padding-bottom: 15px;

  font-size: 24px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;

  border: 1px solid #cfcfcf;
  border-radius: 5px;
}

.example__item:nth-child(1)::after { // Надпись "Снижение веса"
  content: "снижение веса";

  position: absolute;

  width: 80px;

  bottom: -17px;
  right: 22px;

  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-transform: none;

  background-color: #f2f2f2;
}

.example__item:nth-child(2)::after { // Надпись "Затрачено времени"
  content: "затрачено времени";

  position: absolute;

  width: 80px;

  bottom: -17px;
  right: 22px;

  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;

  text-transform: none;

  background-color: #f2f2f2;
}

.example__cost { // Стоимость программы
  width: 280px;

  margin: 0;
  margin-bottom: 33px;
  padding: 0 0 0 20px;

  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: 768px) { // Если экран больше 768px ----->
  .example { // Меняем размеры блока
    background: none;

    border-top: 1px solid #e6e6e6;
  }

  .example__wrapper {
    width: 100%;
  }

  .example__container { // Отступы контейнера
    width: 750px;
  }

  .example__header { // Заголовок
    width: 708px;

    margin: 42px 0 65px 0;
    padding: 0 0 0 32px;

    font-size: 60px;
    line-height: 60px;
  }

  .example__info { // "Борис сбросил 5 кг...."
    width: 680px;

    margin-bottom: 52px;
    padding: 0 0 0 32px;

    font-size: 16px;
    line-height: 24px;
  }

  .example__stats { // Список характеристик
    width: 365px;

    padding: 0 0 0 32px;
  }

  .example__item { // "5 кг, 60 дней"
    width: 160px;

    padding: 17px 0 17px 0;

    font-size: 30px;
    line-height: 37px;
  }

  .example__item:nth-child(1)::after { // "Снижение веса"
    bottom: -6px;
    right: 23px;

    width: 115px;

    font-size: 14px;
    line-height: 14px;

    background: #ffffff;
  }

  .example__item:nth-child(2)::after { // "Затрачено времени"
    bottom: -6px;
    left: 13px;

    width: 135px;

    font-size: 14px;
    line-height: 14px;

    background: #ffffff;
  }

  .example__cost { // Стоимость программы
    margin-left: 147px;
    padding: 0;

    width: 195px;

    font-size: 20px;
    line-height: 26px;
    text-align: right;
  }

  .example__cost span { // Стоимость программы
    display: block;
    margin-top: 3px;
  }
}

@media (min-width: 1300px) { // Если экран больше 768px ----->
  .example { // Меняем размеры блока
    position: relative;
    margin: 199px 0 0 0;
    padding-bottom: 0;

    background-color: #f2f2f2;

    border: none;
    z-index: 0;
  }

  .example__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;

    width: 1283px;
    margin: 0 auto;
  }

  .example__container { // размеры и отступы контейнера
    flex-direction: column;
    align-items: flex-start;

    width: 570px;

    margin: -134px 0 0 0;

    padding: 0;
  }

  .example__header { // Заголовок
    width: 570px;
    margin: 0 0 146px 3px;
    padding: 0 0 0 40px;
  }

  .example__info { // "Борис сбросил 5 кг...."
    width: 435px;

    margin-bottom: 68px;
    margin-left: 3px;
    padding: 0 0 0 40px;
  }

  .example__stats { // Список характеристик
    width: 367px;

    margin-bottom: 57px;
    padding: 0 0 0 42px;
  }

  .example__item:nth-child(1)::after { // "Снижение веса"
    background: #f2f2f2;
    bottom: -6px;
    left: 22px;
  }

  .example__item:nth-child(2)::after { // "Затрачено времени"
    background: #f2f2f2;
    bottom: -6px;
    left: 11px;
  }

  .example__cost { // Стоимость программы
    width: 330px;

    margin: 0;
    padding-left: 42px;

    text-align: left;
  }

  .example__cost span { // Надпись "15000 рублей"
    display: inline;
    padding-left: 60px;
  }
}
